import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
Vue.config.productionTip = false

import { Icon, Card, Stepper  } from 'vant';
Vue.use(Icon).use(Card).use(Stepper);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
